import {
	ApplicationRef,
	ComponentFactoryResolver,
	ComponentRef,
	Directive,
	ElementRef,
	EmbeddedViewRef,
	Inject,
	Injector,
	OnDestroy,
	Type
} from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { FabricReactive } from '../fabric-reactive';
import { FabricModalConfig } from './fabric-modal.config';
import { fromEvent, Observable } from 'rxjs';
import { filter, takeUntil } from 'rxjs/operators';

@Directive()
export abstract class FabricModal<T> extends FabricReactive implements OnDestroy {

	private componentRef: ComponentRef<T> | 'destroyed' = 'destroyed';

	protected constructor(private readonly componentFactoryResolver: ComponentFactoryResolver,
						  private readonly applicationRef: ApplicationRef,
						  private readonly injector: Injector,
						  @Inject(DOCUMENT) private readonly document: any) {
		super();
	}

	ngOnDestroy() {
		this.removeComponent();
	}

	abstract getComponent(): Type<T>;

	createAndAppend(config?: FabricModalConfig): void {
		const componentInjector = config?.injector ? config.injector : this.injector;

		this.componentRef = this.componentFactoryResolver
								.resolveComponentFactory(this.getComponent())
								.create(componentInjector);

		this.applicationRef.attachView(this.componentRef.hostView);

		const domModalElement = (this.componentRef.hostView as EmbeddedViewRef<any>)
			.rootNodes[0] as HTMLElement;

		this.getElement(config?.appendToElement).appendChild(domModalElement);

		if (config?.afterCompCreation) {
			config?.afterCompCreation();
		}

		this.componentRef.changeDetectorRef.detectChanges();
	}

	removeComponent(): void {
		if (this.componentRef !== 'destroyed') {
			this.applicationRef.detachView(this.componentRef.hostView);
			this.componentRef.destroy();
			this.componentRef = 'destroyed';
			this.unsubscribe();
		}
	}

	getComponentRef(): ComponentRef<T> | 'destroyed' {
		return this.componentRef;
	}

	getInjector(): Injector {
		return this.injector;
	}

	getDocument(): any {
		return this.document;
	}

	onCloseOnEsc(): Observable<any> {
		const close$ = fromEvent<any>(this.getDocument(), 'keyup');
		return close$
			.pipe(
				filter((key) => key.code === 'Escape'),
				takeUntil(this.unsubscribe$)
			);
	}

	isComponentCreated(): boolean {
		return this.componentRef !== 'destroyed';
	}

	private getElement(el?: ElementRef): any {
		return el ? el.nativeElement : this.document.body;
	}
}
