import { ApplicationRef, ComponentFactoryResolver, ComponentRef, Inject, Injectable, Injector, Type } from '@angular/core';
import { FabricModal } from '../../common/modal/fabric-modal';
import { FabricSelectOptionsComponent } from './options/select-options.component';
import { SelectOptionsGeometryService } from './options/select-options-geometry.service';
import { DOCUMENT } from '@angular/common';
import { GuiSelectOption } from './select-option';

@Injectable()
export class SelectOptionModalService extends FabricModal<FabricSelectOptionsComponent> {

	constructor(private readonly selectOptionsGeometryService: SelectOptionsGeometryService,
				componentFactoryResolver: ComponentFactoryResolver,
				applicationRef: ApplicationRef,
				injector: Injector,
				@Inject(DOCUMENT) document: any) {
		super(componentFactoryResolver, applicationRef, injector, document);
	}

	getComponent(): Type<FabricSelectOptionsComponent> {
		return FabricSelectOptionsComponent;
	}

	open(options: Array<GuiSelectOption>, width: number): void {
		this.createAndAppend({
			afterCompCreation: () => this.afterCompCreation(options, width)
		});
	}

	closeOptions(): void {
		this.removeComponent();
	}

	private afterCompCreation(options: Array<GuiSelectOption>, width: number): void {
		if (this.isComponentCreated()) {
			const componentRef = this.getComponentRef() as ComponentRef<FabricSelectOptionsComponent>;
			componentRef.instance.options = options;
			componentRef.instance.width = width;
			componentRef.instance.detectChanges();
			this.selectOptionsGeometryService.setGeometry(componentRef.instance.getElementRef());
			componentRef.instance.detectChanges();
		}
	}
}
