import { Directive, ElementRef, Input, Renderer2, SimpleChanges } from '@angular/core';

@Directive()
export abstract class Indicator {

	private static readonly PRIMARY_CLASS_NAME = 'gui-primary';

	private static readonly SECONDARY_CLASS_NAME = 'gui-secondary';

	private static readonly OUTLINE_CLASS_NAME = 'gui-outline';

	@Input()
	primary: boolean = false;

	@Input()
	secondary: boolean = false;

	@Input()
	outline: boolean = false;

	protected constructor(protected elementRef: ElementRef,
						  protected renderer: Renderer2) {
	}

	ngOnChanges(changes: SimpleChanges) {

		if (changes['primary']) {
			if (this.primary) {
				this.addClass(Indicator.PRIMARY_CLASS_NAME);
			} else {
				this.removeClass(Indicator.PRIMARY_CLASS_NAME);
			}
		}

		if (changes['secondary']) {
			if (this.secondary) {
				this.addClass(Indicator.SECONDARY_CLASS_NAME);
			} else {
				this.removeClass(Indicator.SECONDARY_CLASS_NAME);
			}
		}

		if (changes['outline']) {
			if (this.outline) {
				this.addClass(Indicator.OUTLINE_CLASS_NAME);
			} else {
				this.removeClass(Indicator.OUTLINE_CLASS_NAME);
			}
		}
	}

	protected addClass(className: string): void {
		this.renderer.addClass(this.elementRef.nativeElement, className);
	}

	protected removeClass(className: string): void {
		this.renderer.removeClass(this.elementRef.nativeElement, className);
	}
}
