import { ApplicationRef, ComponentFactoryResolver, ComponentRef, Inject, Injectable, Injector, Type } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { timer } from 'rxjs';

import { FabricDialogComponent } from './fabric-dialog.component';
import { Theme } from '../../themes/theme';
import { themeToken } from '../../themes/theme.token';
import { FabricModal } from '../../common/modal/fabric-modal';
import { FabricDialogConfig } from './fabric-dialog.config';
import { takeUntil } from 'rxjs/operators';


@Injectable()
export class FabricDialogService extends FabricModal<FabricDialogComponent> {

	constructor(componentFactoryResolver: ComponentFactoryResolver,
				applicationRef: ApplicationRef,
				injector: Injector,
				@Inject(DOCUMENT) document: any) {
		super(componentFactoryResolver, applicationRef, injector, document);
	}

	getComponent(): Type<FabricDialogComponent> {
		return FabricDialogComponent;
	}

	open(config: FabricDialogConfig): void {

		if (this.isComponentCreated()) {
			return;
		}

		let theme = Theme.FABRIC,
			parentInjector = this.getInjector();

		if (config && config.theme) {
			theme = config.theme;
		}

		if (config && config.injector) {
			parentInjector = config.injector;
		}

		const injector = Injector.create({
			providers: [{
				provide: themeToken,
				useValue: theme
			}],
			parent: parentInjector
		});

		this.createAndAppend({
			afterCompCreation: () => this.afterComponentCreation(config),
			injector
		});
		this.closeOnEscKey();
	}

	close(): void {
		if (this.isComponentCreated()) {
			const componentRef = this.getComponentRef() as ComponentRef<FabricDialogComponent>;
			componentRef.instance.visible = false;
			componentRef.instance.detectChanges();

			timer(400)
				.pipe(takeUntil(this.unsubscribe$))
				.subscribe(() => {
					this.removeComponent();
				});
		}
	}

	private closeOnEscKey(): void {
		this.onCloseOnEsc()
			.subscribe(() => this.close());
	}

	private afterComponentCreation(config: FabricDialogConfig): void {
		if (this.isComponentCreated()) {
			const componentRef = this.getComponentRef() as ComponentRef<FabricDialogComponent>;
			componentRef.instance.dialogNestedComponent = config.component;

			if (config?.width) {
				componentRef.instance.width = config.width;
			}
			if (config?.height) {
				componentRef.instance.height = config.height;
			}

			componentRef.instance.setTransformOrigin(event);
		}
	}
}
