import {
	ApplicationRef,
	ComponentFactoryResolver,
	ComponentRef,
	Directive,
	ElementRef,
	EmbeddedViewRef,
	Inject,
	Injector,
	Input,
	OnDestroy,
	OnInit,
	PLATFORM_ID
} from '@angular/core';
import { DOCUMENT, isPlatformBrowser } from '@angular/common';
import { fromEvent } from 'rxjs';

import { FabricTooltipComponent } from './fabric-tooltip.component';
import { FabricReactive } from '../../common/fabric-reactive';
import { takeUntil } from 'rxjs/operators';


@Directive({
	selector: '[gui-tooltip]',
	exportAs: 'guiTooltip'
})
export class FabricTooltipDirective extends FabricReactive implements OnInit, OnDestroy {
	private static readonly tooltipOffset: number = 8;

	@Input('gui-tooltip')
	text = '';

	tooltipRef: ComponentRef<FabricTooltipComponent> | null = null;

	tooltipTopPosition: number | undefined;

	tooltipLeftPosition: number | undefined;

	constructor(private readonly componentFactoryResolver: ComponentFactoryResolver,
				private readonly injector: Injector,
				private readonly elementRef: ElementRef,
				private readonly applicationRef: ApplicationRef,
				@Inject(DOCUMENT) private readonly document: any,
				@Inject(PLATFORM_ID) private readonly platformId: any) {
		super();
	}

	ngOnInit(): void {
		const open$ = fromEvent<any>(this.elementRef.nativeElement, 'mouseenter'),
			close$ = fromEvent<any>(this.elementRef.nativeElement, 'mouseleave');

		open$
			.pipe(takeUntil(this.unsubscribe$))
			.subscribe(() => this.show());

		close$
			.pipe(takeUntil(this.unsubscribe$))
			.subscribe(() => {
				if (this.tooltipRef) {
					this.hide();
				}
			});
	}

	private show(): void {
		const tooltipRef = this.componentFactoryResolver
							   .resolveComponentFactory(FabricTooltipComponent)
							   .create(this.injector);

		tooltipRef.instance.text = this.text;

		tooltipRef.changeDetectorRef.detectChanges();

		const domElement = (tooltipRef.hostView as EmbeddedViewRef<any>)
			.rootNodes[0] as HTMLElement;

		this.document.body.appendChild(domElement);

		this.tooltipRef = tooltipRef;

		this.calculateCords();

		this.tooltipRef.instance.correctPosition();
		this.tooltipRef.changeDetectorRef.detectChanges();
	}

	private hide(): void {
		if (this.tooltipRef) {
			this.applicationRef.detachView(this.tooltipRef.hostView);
			this.tooltipRef.destroy();
			this.tooltipRef = null;
		}
	}

	private calculateCords(): void {
		if (this.tooltipRef) {
			const elementRef = this.elementRef.nativeElement,
				elementRect = elementRef.getBoundingClientRect(),
				elementBottom = elementRect.bottom,
				elementLeft = elementRect.left;

			if (isPlatformBrowser(this.platformId)) {

				this.tooltipTopPosition =
					elementBottom + window.scrollY
					- elementRef.offsetHeight
					- FabricTooltipDirective.tooltipOffset;

				this.tooltipLeftPosition = window.scrollX + elementLeft + elementRef.offsetWidth / 2;

				if (this.tooltipTopPosition) {
					this.tooltipRef.instance.top = this.tooltipTopPosition;
				}

				if (this.tooltipLeftPosition) {
					this.tooltipRef.instance.left = this.tooltipLeftPosition;
				}

				this.tooltipRef.changeDetectorRef.detectChanges();
			}
		}
	}

}
