import {
	AfterViewInit,
	ChangeDetectionStrategy,
	ChangeDetectorRef,
	Component,
	ComponentFactoryResolver,
	ElementRef,
	forwardRef,
	Inject,
	PLATFORM_ID,
	Renderer2,
	Type,
	ViewChild,
	ViewContainerRef,
	ViewEncapsulation
} from '@angular/core';

import { FabricDialogService } from './fabric-dialog.service';
import { FabricNestedDialogComponent } from '../common/fabric.nested-dialog.component';
import { FabricThemedComponent } from '../../common/modal/fabric-themed-component';
import { FabricModalThemeService } from '../../themes/fabric-modal-theme.service';
import { timer } from 'rxjs';
import { isPlatformBrowser } from '@angular/common';
import { takeUntil } from 'rxjs/operators';

@Component({
	selector: 'gui-fabric-dialog',
	templateUrl: './fabric-dialog.component.html',
	styleUrls: [
		'./fabric-dialog.scss',
		'./themes/fabric-dialog.dark.ngx.scss'
	],
	host: {
		'[class.gui-fabric-dialog]': 'true'
	},
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class FabricDialogComponent extends FabricThemedComponent implements AfterViewInit {

	@ViewChild('container', { read: ViewContainerRef, static: false })
	readonly container?: ViewContainerRef;

	dialogNestedComponent?: Type<FabricNestedDialogComponent>;

	visible: boolean = false;

	width: string = '400px';

	height: string = '90vh';

	triggerPosition: string = '';

	constructor(private readonly componentFactoryResolver: ComponentFactoryResolver,
				private readonly changeDetectorRef: ChangeDetectorRef,
				private readonly elRef: ElementRef,
				@Inject(PLATFORM_ID) private platformId: any,
				themeModalService: FabricModalThemeService,
				renderer: Renderer2,
				@Inject(forwardRef(() => FabricDialogService)) private readonly dialogService: FabricDialogService) {
		super(elRef, renderer, themeModalService);
	}

	ngAfterViewInit(): void {
		super.ngAfterViewInit();

		this.createNestedComponent();

		timer(0)
			.pipe(takeUntil(this.unsubscribe$))
			.subscribe(() => {
				this.visible = true;
				this.detectChanges();
			});
	}

	detectChanges(): void {
		this.changeDetectorRef.detectChanges();
	}

	closeDialog(): void {
		this.dialogService.close();
	}

	clickOutside(event: any): void {
		if (this.isContainerClicked(event)) {
			this.dialogService.close();
		}
	}

	setTransformOrigin(event: any): void {
		if (isPlatformBrowser(this.platformId) && event) {
			const x = (event.clientX - (window.innerWidth / 2)),
				y = (event.clientY - (window.innerHeight / 2));

			this.triggerPosition = `${x}px ${y}px`;
		}
	}

	private isContainerClicked(event: any): boolean {
		const dialogContentRef = this.elRef.nativeElement.querySelector('.gui-dialog-content');

		if (dialogContentRef) {
			return !dialogContentRef.contains(event.target);
		}

		return false;
	}

	private createNestedComponent(): void {
		if (this.dialogNestedComponent && this.container) {
			const componentFactory = this.componentFactoryResolver.resolveComponentFactory(this.dialogNestedComponent);

			this.container.createComponent(componentFactory);
			this.detectChanges();
		}
	}


}
