import {
	AfterViewInit,
	ChangeDetectionStrategy,
	ChangeDetectorRef,
	Component,
	ElementRef,
	EventEmitter,
	Input,
	OnChanges,
	OnDestroy,
	OnInit,
	Output,
	SimpleChanges,
	ViewChild,
	ViewEncapsulation
} from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { skip, take, takeUntil } from 'rxjs/operators';

import { FabricDatePickerCalendarComponent } from './calendar/fabric-date-picker-calendar.component';
import { FabricDatePickerService } from './fabric-date-picker.service';
import { FabricDatePickerInlineDialogService } from './fabric-date-picker-inline-dialog.service';
import { FabricReactive } from '../../common/fabric-reactive';

import { Theme } from '../../themes/theme';
import { FabricDatePickerCompositionService } from './fabric-date-picker-composition.service';
import { FabricDateUtils } from '../../common/date-utils/fabric-date-utils';

@Component({
	selector: 'gui-date-picker',
	templateUrl: './fabric-date-picker.component.html',
	styleUrls: [
		'./fabric-date-picker.scss',
		'./themes/fabric-date-picker.dark.scss',
		'./themes/fabric-date-picker.material.scss'
	],
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class FabricDatePickerComponent extends FabricReactive implements OnChanges, OnInit, AfterViewInit, OnDestroy {

	@ViewChild('datePicker', { read: ElementRef, static: false })
	readonly datePickerRef?: ElementRef;

	@Input()
	parentElement?: ElementRef;

	@Input()
	theme?: Theme;

	@Input()
	selectDate?: Date;

	@Input()
	name: string = '';

	@Input()
	openDialog: boolean = false;

	@Input()
	onlyDialog: boolean = false;

	@Input()
	datePipeOptions: string = 'dd/MM/yyyy';

	@Output()
	readonly dateSelected = new EventEmitter();

	@Output()
	readonly dialogOpened = new EventEmitter();

	datePickerForm: FormGroup;

	pickedDate: Date = new Date();

	inputDisabled: 'disabled' | '' = '';

	constructor(private readonly fabricDatePickerInlineDialogService: FabricDatePickerInlineDialogService,
				private readonly datePickerService: FabricDatePickerService,
				private readonly datePickerCompositionService: FabricDatePickerCompositionService,
				formBuilder: FormBuilder,
				private readonly changeDetectorRef: ChangeDetectorRef) {
		super();
		this.datePickerForm = formBuilder.group({
				date: ['']
			}
		);
	}

	ngOnChanges(changes: SimpleChanges): void {

		if (changes['selectDate']) {
			if (this.selectDate) {
				if (!this.pickedDate || !FabricDateUtils.areDatesSame(this.selectDate, this.pickedDate)) {
					this.datePickerService.dateSelected(this.selectDate);
				}
			}
		}

		if (changes['onlyDialog']) {
			this.inputDisabled = this.onlyDialog ? 'disabled' : '';
		}

		if (changes['datePipeOptions']) {
			this.datePickerCompositionService.next(this.datePipeOptions);
		}
	}

	ngOnInit(): void {
		this.datePickerService
			.observeSelectedDate()
			.pipe(
				skip(1),
				takeUntil(this.unsubscribe$)
			)
			.subscribe((date: Date) => {
					this.pickedDate = date;
					this.emitSelectedDate(date);
					this.changeDetectorRef.detectChanges();
					this.closeDatePicker();
				}
			);

		this.fabricDatePickerInlineDialogService
			.onOpened()
			.pipe(
				skip(1),
				takeUntil(this.unsubscribe$)
			)
			.subscribe((opened: boolean) => {
				this.dialogOpened.emit(opened);
			});

		// this.observeDayChanges();
	}

	ngAfterViewInit(): void {
		this.datePickerService
			.observeSelectedDate()
			.pipe(
				take(1),
				takeUntil(this.unsubscribe$)
			)
			.subscribe(
				(date: Date) => {
					this.pickedDate = date;
					this.emitSelectedDate(date);
					this.changeDetectorRef.detectChanges();
				}
			);

		if (this.openDialog) {
			this.openDatePicker();
		}
	}

	ngOnDestroy(): void {
		super.ngOnDestroy();
		this.fabricDatePickerInlineDialogService.close();
	}

	openDatePicker(): void {
		const shouldUseDatePickerRef = !this.parentElement && this.datePickerRef,
			parentElement = shouldUseDatePickerRef ? this.datePickerRef : this.parentElement;

		if (parentElement) {
			this.fabricDatePickerInlineDialogService
				.open(
					parentElement,
					FabricDatePickerCalendarComponent,
					this.theme
				);
		}
	}

	closeDatePicker(): void {
		this.fabricDatePickerInlineDialogService.close();
	}

	private emitSelectedDate(date: Date): void {
		this.dateSelected.emit(date);
	}
}

