import { ApplicationRef, ComponentFactoryResolver, ComponentRef, Inject, Injectable, Injector, OnDestroy, Type } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { timer } from 'rxjs';

import { FabricDrawerComponent } from './fabric-drawer.component';
import { Theme } from '../../themes/theme';
import { themeToken } from '../../themes/theme.token';
import { FabricModal } from '../../common/modal/fabric-modal';
import { FabricDrawerConfig } from './fabric-drawer.config';
import { takeUntil } from 'rxjs/operators';


@Injectable()
export class FabricDrawerService extends FabricModal<FabricDrawerComponent> implements OnDestroy {

	/** To prevent multiple timers **/
	inProgress: boolean = false;

	constructor(componentFactoryResolver: ComponentFactoryResolver,
				applicationRef: ApplicationRef,
				injector: Injector,
				@Inject(DOCUMENT) document: any) {
		super(componentFactoryResolver, applicationRef, injector, document);
	}

	getComponent(): Type<FabricDrawerComponent> {
		return FabricDrawerComponent;
	}

	open(config: FabricDrawerConfig): void {
		if (event) {
			event.stopPropagation();
		}

		if (this.isComponentCreated() && config?.preventReopeningDrawer) {
			return;
		}

		if (this.isComponentCreated() && !this.inProgress && !config?.preventReopeningDrawer) {
			this.waitAndCreateNewDrawer(config);
		} else {
			this.createDrawer(config);
		}
	}

	close(): void {
		if (this.isComponentCreated()) {
			const componentRef = this.getComponentRef() as ComponentRef<FabricDrawerComponent>;
			componentRef.instance.visible = false;
			componentRef.instance.detectChanges();

			timer(300)
				.pipe(takeUntil(this.unsubscribe$))
				.subscribe(() => {
					this.removeComponent();
					this.inProgress = false;
				});
		}
	}

	private createDrawer(config: FabricDrawerConfig): void {

		if (this.isComponentCreated()) {
			this.removeComponent();
		}

		let theme = Theme.FABRIC,
			parentInjector = this.getInjector();

		if (config && config.theme) {
			theme = config.theme;
		}

		if (config && config.injector) {
			parentInjector = config.injector;
		}

		const injector = Injector.create({
			providers: [{
				provide: themeToken,
				useValue: theme
			}],
			parent: parentInjector
		});

		this.createAndAppend({
			injector: injector,
			afterCompCreation: () => this.applyInstanceVars(config),
			appendToElement: config?.appendToElement
		});
		this.closeOnEscKey();
	}

	private waitAndCreateNewDrawer(config: FabricDrawerConfig): void {
		this.inProgress = true;
		this.close();

		timer(400)
			.pipe(
				takeUntil(this.unsubscribe$)
			)
			.subscribe(() => {
				this.createDrawer(config);
			});
	}

	private applyInstanceVars(config: FabricDrawerConfig): void {
		if (this.isComponentCreated()) {
			const componentRef = this.getComponentRef() as ComponentRef<FabricDrawerComponent>;
			if (config?.width) {
				componentRef.instance.width = config.width;
			}

			if (config?.closeOnClickOutside) {
				componentRef.instance.closeOnClickOutside = config.closeOnClickOutside;
			}

			componentRef.instance.dialogNestedComponent = config?.component;
			componentRef.instance.isFixed = !config?.appendToElement;
		}
	}

	private closeOnEscKey(): void {
		this.onCloseOnEsc()
			.subscribe(() => this.close());
	}
}
