import { AfterViewInit, ChangeDetectionStrategy, Component, ElementRef, Input, ViewChild, ViewEncapsulation } from '@angular/core';

@Component({
	selector: 'gui-svg-template',
	template: `
		<div #svgEl></div>
	`,
	encapsulation: ViewEncapsulation.None,
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class FabricSvgTemplate implements AfterViewInit {

	@ViewChild('svgEl', { read: ElementRef, static: false })
	readonly svgRef?: ElementRef;

	@Input()
	svg: string = '';

	// todo sanitize
	ngAfterViewInit(): void {
		if (this.svgRef) {
			this.svgRef.nativeElement.innerHTML = this.svg;
		}
	}
}
